.modal-location{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: center;
    pointer-events: none;
    overflow:hidden;
    width: 100%;
    height: 100%;
    background-color: #FFB8CA;
    padding: 0;
  
    opacity: 0;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and(min-width:480px) {
        width: 470px;}
        
    @media screen and(min-width:768px) {
            width: 758px;}

    @media screen and(min-width:1280px) {
                width: 1270px;}

    &__map {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 100%;
    }
}

.modal-location.target {

    opacity: 1; /* делаем окно видимым */
    pointer-events: auto; /* элемент видим для событий мыши */
    overflow-y: auto; /* добавляем прокрутку по y, когда элемент не помещается на страницу */
    z-index: 99;
} 


.modal-location__button {
    @extend %button-ice;
}

.video{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    left: 0;
    right: 0;
    width:90%;
    height:90%;

